/* Header container */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  flex-wrap: wrap;
  box-sizing: border-box;

  /* Sticky + visual effects */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;

  /* Background & shadow */
  background: linear-gradient(
    90deg,
    rgba(4, 30, 137, 0.95),
    rgba(4, 30, 137, 0.9)
  );
  backdrop-filter: blur(6px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.25);

  /* Smooth transitions */
  transition: background 0.3s ease, box-shadow 0.3s ease, padding 0.3s ease;
}

/* Optional: Shrink header on scroll */
.header.scrolled {
  padding: 10px 20px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.35);
}

/* Left: Logo */
.header-left {
  flex: 1 1 auto;
  display: flex;
  align-items: center;
}

.logo-text {
  color: white;
}

.logo {
  /* Container for the logo image */
  transition: all 0.3s ease;
}

.logo img {
  height: 48px; /* Adjusted to match search field height */
  width: auto;
  display: block;
  transition: height 0.3s ease;
}

/* Optional: shrink on scroll */
.header.scrolled .logo img {
  height: 40px;
}

/* Center: Search field */
.header-center {
  flex: 2 1 auto;
  display: flex;
  justify-content: center;
  margin: 10px 0;
  z-index: 1001;
}

/* Right: Nav + hamburger */
.header-right {
  flex: 1 1 auto;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
}

/* Nav links */
.nav {
  display: flex;
  gap: 15px;
}

.nav a {
  color: var(--color-text-primary, #fff);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
}

.nav a:hover {
  color: var(--color-link, #00bfff);
}

.nav a.active {
  font-weight: bold;
}

/* Hamburger menu */
.hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
  gap: 4px;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease;
}

.hamburger div {
  width: 25px;
  height: 3px;
  background-color: var(--color-text-primary, #fff);
  transition: all 0.3s ease;
}

.hamburger:hover div {
  background-color: var(--color-link, #00bfff);
}

/* Responsive styles */
@media (max-width: 768px) {
  .header {
    flex-direction: row;
    align-items: center;
    padding: 10px 15px;
    min-height: 60px;
  }

  /* Hide only logo image on mobile, keep text visible */
  .header-left .logo {
    display: none;
  }

  /* Compact text styling */
  .header-left {
    flex: 0 0 auto;
    padding-right: 10px;
  }

  .logo-text {
    font-size: 16px;
    font-weight: bold;
  }

  /* Compact search field */
  .header-center {
    flex: 1;
    margin: 0 8px;
  }

  .header-center .search-input {
    width: 100%;
    height: 36px;
    font-size: 14px;
    padding: 6px 10px;
  }

  .header-center .search-button {
    height: 36px;
    font-size: 14px;
    padding: 0 8px;
  }

  .header-right {
    flex: 0 0 auto;
    height: 40px;
  }

  .nav {
    display: none;
    flex-direction: column;
    gap: 10px;
    background: rgba(4, 30, 137, 0.95);
    padding: 10px 0;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(6px);
  }

  .nav.open {
    display: flex;
  }

  .hamburger {
    display: flex;
    order: 1;
  }
}
